import React from "react";
import syllabus from "./pdf/ICT 116_BSCS_1st Sem, AY 2020-2021.pdf";
import "../components/styles/landingpage.css";
import { Link } from "react-router-dom";
const LandingPage = () => {
  return (
    <div className="landingpage-body">
      <div className="side-container">
        <div className="leftside">
          <div className="title-div"></div>
          <div className="button-div">
            <div
              className="handlerButtons"
              type="button"
              id="moduleAssignmentsBtn"
            >
              <Link
                to="/eportfolioapp/moduleAssignments.jsx"
                className="handlerButtonsLink"
              >
                Assignment / Modules
              </Link>
            </div>
            <button
              className="handlerButtons"
              type="button"
              id="syllabusBtn"
              onClick={() => window.open(syllabus)}
            >
              Syllabus
            </button>
          </div>
        </div>
        <div className="rightside">
          <div className="topside"></div>
          <div className="bottomside"></div>
        </div>
      </div>
    </div>
  );
};

export default LandingPage;
